import { getDoc, doc } from "firebase/firestore";
import { f as firebase } from "../../../../../chunks/firebase.js";
import { error } from "@sveltejs/kit";
const load = ({ params }) => {
  return (async () => {
    const user = (await getDoc(doc(firebase.db, `/Users/${params.id}`))).data();
    if (!user) {
      error(404, "user not found");
    }
    return {
      user
    };
  })();
};
export {
  load
};
